import { FaTelegramPlane, FaWhatsapp } from "react-icons/fa";
import { MdLocalPhone, MdLocationOn } from "react-icons/md";
import YandexMap from "./YandexMap";

const Contacts = () => {
  return (
    <section id="contacts">
      <h1>Контакты</h1>
      <div className="wrapper">
        <div className="contacts-container">
          <article>
            <MdLocationOn />
            г. Ростов-на-дону, <br />
            ул. Обсерваторная, д.42
          </article>
          <article className="phone">
            <MdLocalPhone /> +7 (863) 220-77-20
          </article>
          <a href="https://t.me/BAERSON">
            <article className="telegram">
              <FaTelegramPlane />
              Telegram
            </article>
          </a>
          <a href="https://api.whatsapp.com/send?phone=89281797508">
            <article className="whatsapp">
              <FaWhatsapp />
              Whatsapp
            </article>
          </a>
        </div>
        <div className="map-container">
          <YandexMap />
        </div>
      </div>
    </section>
  );
};

export default Contacts;
