import { Map, Placemark, YMaps } from "@pbe/react-yandex-maps";

const YandexMap = () => {
  const center = [47.3105415, 39.7303756];

  return (
    <YMaps>
      <Map width={"100%"} height={300} defaultState={{ center, zoom: 14 }}>
        <Placemark geometry={center} />
      </Map>
    </YMaps>
  );
};

export default YandexMap;
